import React from "react";
import "./navbar.scss";
import logo from "../logo.png";

export default function Navbar() {
  return (
    <nav className="navbar">
      <img src={logo} className="image" alt="Wednesday Logo" />
      
        <li>
          
          <a href="/" className="nav-link" />
         
        </li>{" "}
    
    </nav>
  );
}
